/* 상단 버튼 및 검색바 컨테이너 */
.search-and-buttons {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

/* 각 버튼의 기본 스타일 */
.search-and-buttons button {
    margin-right: 10px;
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.search-and-buttons button:last-child {
    margin-right: 0; /* 마지막 버튼은 여백 제거 */
}

.search-and-buttons button:hover {
    background-color: #0056b3;
}

/* 검색바 스타일 */
.search-and-buttons input {
    padding: 5px;
    width: 300px;
}

/* 전체 리스트 컨테이너 */
.market-lists {
    font-size: 13px;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    max-width: 1200px;
    margin: 0 auto;
}

/* 각 마켓 리스트 (KRW, BTC, USDT) */
.market-list {
    border: 1px solid black;
    padding: 10px;
    max-width: 30%;
    flex: 1;
}

.market-list h3 {
    margin-bottom: 10px;
}

/* 리스트 아이템 */
.market-item {
    display: flex;
    border-bottom: 1px solid black;
    padding: 10px;
}

.market-item div {
    flex: 1;
    padding: 5px;
    word-wrap: break-word;
    border-right: 1px solid black;
}

.market-item div:last-child {
    border-right: none; /* 마지막 항목은 테두리 제거 */
}


/* 검색바와 버튼, 필터 컨테이너 */
.search-and-buttons {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center; /* 검색창, 필터, 버튼들이 수직으로 중앙 정렬됩니다 */
}

/* 각 버튼의 기본 스타일 */
.search-and-buttons button {
    margin-right: 10px;
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.search-and-buttons button:last-child {
    margin-right: 0;
}

.search-and-buttons button:hover {
    background-color: #0056b3;
}

/* 검색바 스타일 */
.search-and-buttons input {
    padding: 5px;
    width: 300px;
    margin-right: 10px;
}

/* 필터 스타일 */
.search-and-buttons select {
    padding: 5px;
    margin-left: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
}

/* 전체 리스트 컨테이너 */
.market-lists {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    max-width: 1200px;
    margin: 0 auto;
}


/* 전체 리스트 컨테이너 */
.market-lists {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    max-width: 1200px;
    margin: 0 auto;
    gap: 20px; /* 리스트 사이의 간격 */
}

/* 각 마켓 리스트 (KRW, BTC, USDT) */
.market-list {
    border: 1px solid black;
    padding: 10px;
    max-width: 30%;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    height: 100%; /* 높이를 동일하게 */
}

/* 리스트 아이템 */
.market-item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    padding: 10px;
    align-items: center;
    height: 50px; /* 각 아이템의 높이를 고정 */
}

/* 각 칸의 내용이 중앙 정렬되도록 */
.market-item div {
    flex: 1;
    text-align: center;
    word-wrap: break-word;
}

/* 마지막 아이템의 경계선 제거 */
.market-item:last-child {
    border-bottom: none;
}
